import Pattern from "./Pattern.js";

export default class Optional extends CompositePattern {
  constructor(pattern) {
    this.pattern = pattern;
    this.assertArguments();
  }

  assertArguments() {
    if (!(this.pattern instanceof Pattern)) {
      throw new Error("Invalid Arguments: Expected a Pattern.");
    }
  }

  getName() {
    return this.pattern.getName();
  }

  getType() {
    return this.pattern.getType();
  }

  getPatterns() {
    return [this.pattern];
  }

  parse(cursor) {
    const mark = cursor.mark();

    try {
      return this.pattern.parse(cursor);
    } catch (error) {
      cursor.moveToMark(mark);
      return null;
    }
  }

  clone() {
    return new Optional(this.pattern);
  }
}
